import { Component, ViewChild, ViewChildren, QueryList } from '@angular/core';
import { AlertController, IonicPage, ModalController, NavController, NavParams, Slides } from 'ionic-angular';

/**
 * Generated class for the NewTaptopayPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-new-taptopay',
  templateUrl: 'new-taptopay.html',
})
export class NewTaptopayPage {
  @ViewChild(Slides) slides: Slides;
  sof = [
    {
      Card_Type: "S",
      acct_alias: "C03632023071713020089592",
      Cust_Name: "HARJONO SIDHODJOJO",
      type_product: "KARTU KREDIT VISA PLATINUM 1  ",
      Card_No: "4890870052090363",
      imgSrc: "assets/imgs/sofCard2/cc_barca.jpg",
      maskCardNo: "489087xxxxxx0363",
      vCardId: "vLUYhE9VB0OJDNv6106VeQc=",
      default: false
    },
    {
      Card_Type: "C",
      acct_alias: "C03632023071713020089592",
      Cust_Name: "HARJONO SIDHODJOJO",
      type_product: "KARTU KREDIT CARREFOUR 1  ",
      Card_No: "489087005209999",
      imgSrc: "assets/imgs/sofCard2/cc-bliv.png",
      maskCardNo: "489087xxxxxx9999",
      vCardId: "vLUYhE9VB0OJDNv6106VeQc=",
      default: true
    }
  ]

  activeSofIndex: any;
  defaultSofIndex: any = 1;
  defaultSof: any;
  modalCardNumber: boolean = false;
  noCard = "**** **** **** 1290";
  currentSofIndex: any;
  isAddCard: boolean = false;

  spaceBetween = 3;

  constructor(public navCtrl: NavController, public navParams: NavParams) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad NewTaptopayPage');
    this.checkScreen();
    var myPath = document.getElementById("tick") ;
    console.log(myPath, 'my');
  }

  ionViewWillEnter() {
    console.log('ionViewWillEnter NewTaptopayPage');
  }

  checkScreen() {
    let innerWidth = window.innerWidth;
    console.log('width', innerWidth);

    switch (true) {
      case innerWidth <= 350:
        console.log('0')
        this.spaceBetween = -75;
        return;
      case 351 <= innerWidth && innerWidth <= 400:
        console.log('1')
        this.spaceBetween = -90;
        return;
      case 401 <= innerWidth && innerWidth <= 500:
        console.log('2')
        this.spaceBetween = -120;
        return;
      case 501 <= innerWidth && innerWidth <= 900:
        console.log('3')
        this.spaceBetween = -310;
        return;
      case 901 <= innerWidth:
        return 8;
    }
  }

  slideChanged() {
    let currentIndex = this.slides.getActiveIndex();
    let lastIndex = this.slides.getActiveIndex();

    this.activeSofIndex = currentIndex;

    console.log('Current index is', currentIndex, typeof currentIndex);
    
    if (lastIndex) {
      this.isAddCard = true;
    } else {
      this.isAddCard = false;
    }

  }

}



